<template>
	<v-app >
		<div>
			<v-container  fluid>
				<v-layout align-center justify-center>
					<v-flex xs12 sm12 md10>
						<v-card class="mx-auto">
							<v-row no-gutters>
								<v-col cols="12">
									<div>
									</div>
									<div>
										<div class="ml-4 mr-4">
											<h3 class="mt-6 ml-5 mb-5 text-center">Blocked list</h3>
										</div>
									</div>
									<v-tabs background-color="white" color="black"  fixed-tabs>
										<v-tab>Call List</v-tab>
										<v-tab>Blocked List</v-tab>
										<v-tab-item v-for="n in 2" :key="n">
											<v-container fluid>
												<v-card-text v-if="n==1">
													<v-card v-for="details in call_list" :key="details.text">
														<div>{{ details.callerNumber }}
															<v-btn color="red" text @click="block_user(details.callerNumber)"> block </v-btn>
															<span class="blue--text">{{ details.conversationduartion }}</span>
														</div>
														<div>{{details.dateTime}}</div>
													</v-card>
												</v-card-text>
												<v-card-text v-if="n==2">
													<v-card v-for="details in blocked_list" :key="details.text">
														<div>{{ details.callerNumber }}
															<v-btn color="red" text @click="unblock_user(details.callerNumber)"> unblock </v-btn>
															<span class="blue--text">{{ details.conversationduartion }}</span>
														</div>
														<div>{{details.dateTime}}</div>
													</v-card>
												</v-card-text>
											</v-container>
										</v-tab-item>
									</v-tabs>
								</v-col>
							</v-row>
						</v-card>
					</v-flex>
				</v-layout>
			</v-container>
		</div>
	</v-app>
</template>


<script>
import firebase from 'firebase'
import { db } from '@/main.js';
import moment from 'moment'
  export default {
		created() {
			firebase.auth().onAuthStateChanged(user => {
				if (user) {
					console.log("logged user details",user)
					this.uid = user.uid
					this.phno = user.phoneNumber.slice(3)
					console.log("page call logs il user id", this.uid)
					console.log("page call logs il user number", this.phno)
					db.collection('users').where("uid" , "==" , this.uid).get().then((querySnapshot) => {
						querySnapshot.forEach((doc) => {
							console.log(doc.id, " => ", doc.data());
							let user_details = doc.data()
							this.virtual_number = user_details.virtualNumber
							console.log(user_details.role)
						})
					})
					.catch((error) => {
						console.log("Error getting documents: ", error);
					});
					db.collection('callLogs').where("userid" , "==" , this.uid).get().then((querySnapshot) => {
						querySnapshot.forEach((doc) => {
							console.log(doc.id, " => ", doc.data());
							let user_details = doc.data()
							this.calldetails = user_details
							var timestamp = this.calldetails.dateTime
							var date = new Date(timestamp);
							console.log("full time",date)
							var call_time = moment(date).format('DD/mm/YYYY hh:mm a')
							console.log("converted time",call_time)
							this.detail = Object.assign({}, this.detail, { callstatus : this.calldetails.callstatus , name: this.calldetails.name ,dateTime : call_time , conversationduration : this.calldetails.conversationduration ,callerNumber : this.calldetails.callerNumber })
							this.call_list.push(this.detail)
							console.log(this.call_list)
						})
					})
					.catch((error) => {
						console.log("Error getting documents: ", error);
					})



					db.collection('blockCalls').where("Uid" , "==" , this.uid).get().then((querySnapshot) => {
						querySnapshot.forEach((doc) => {
							console.log(doc.id, " => ", doc.data());
							let user_details = doc.data()
							this.calldetails = user_details
							var timestamp = this.calldetails.lastCall
							var date = new Date(timestamp);
							console.log("full time",date)
							var call_time = moment(date).format('DD/mm/YYYY hh:mm a')
							console.log("converted time",call_time)
							if(this.calldetails.isBlocked == "true") {
								this.blocked_detail = Object.assign({}, this.blocked_detail, { callstatus : this.calldetails.CallStatus  ,dateTime : call_time , conversationduration : this.calldetails.ConversationDuration ,callerNumber : this.calldetails.Number , isBlocked : this.calldetails.isBlocked})
								this.blocked_list.push(this.blocked_detail)
								// this.userUid = this.Udata.uid
								// console.log(typeof(this.detail))
								console.log('blocked details',this.blocked_list)
							}
						})
					})
					.catch((error) => {
						console.log("Error getting documents: ", error);
					})
				}
			})
		},

		data: () => ({
			uid : '',
			phno : '',
			detail: {},
			blocked_detail: {},
			blocked_list: [],
			calldetails : [],
			call_list : [],
			virtual_number : '',
    }),
    methods :{
			block_user(number){
				const blocked_data = {
					url: 'https://asia-south1-tringpartner-v2.cloudfunctions.net/tpv2/blockcall',
					method: 'POST',
					data: {
						owner_uid: this.uid,
						status: true,
						virtual_number : 9731175690,
						number: number
					},
				}
				console.log(blocked_data)
				this.$axios(blocked_data)
					.then((response) => {
						console.log(response)

						db.collection('blockCalls').where("Uid" , "==" , this.uid).get().then((querySnapshot) => {
							querySnapshot.forEach((doc) => {
								console.log(doc.id, " => ", doc.data());
								let user_details = doc.data()
								this.calldetails = user_details
								var timestamp = this.calldetails.lastCall
								var date = new Date(timestamp);
								console.log("full time",date)
								var call_time = moment(date).format('DD/mm/YYYY hh:mm a')
								console.log("converted time",call_time)
								if(this.calldetails.isBlocked == "true") {
									this.blocked_detail = Object.assign({}, this.blocked_detail, { callstatus : this.calldetails.CallStatus  ,dateTime : call_time , conversationduration : this.calldetails.ConversationDuration ,callerNumber : this.calldetails.Number , isBlocked : this.calldetails.isBlocked})
									this.blocked_list.push(this.blocked_detail)
									// this.userUid = this.Udata.uid
									// console.log(typeof(this.detail))
									console.log('blocked details',this.blocked_list)
								}
							})
						})
					.catch((error) => {
						console.log("Error getting documents: ", error);
					})
					})
					.catch((error) => {blocked_data
						console.error(error);
					})
			},

			unblock_user(number){
				const blocked_data = {
					url: 'https://asia-south1-tringpartner-v2.cloudfunctions.net/tpv2/blockcall',
					method: 'POST',
					data: {
						owner_uid: this.uid,
						status: false,
						virtual_number : 9731175690,
						number: number
					},
				}
				console.log(blocked_data)
				this.$axios(blocked_data)
					.then((response) => {
						console.log(response)
						this.blocked_list = []
						db.collection('blockCalls').where("Uid" , "==" , this.uid).get().then((querySnapshot) => {
							querySnapshot.forEach((doc) => {
								console.log(doc.id, " => ", doc.data());
								let user_details = doc.data()
								this.calldetails = user_details
								var timestamp = this.calldetails.lastCall
								var date = new Date(timestamp);
								console.log("full time",date)
								var call_time = moment(date).format('DD/mm/YYYY hh:mm a')
								console.log("converted time",call_time)
								if(this.calldetails.isBlocked == "true") {
									this.blocked_detail = Object.assign({}, this.blocked_detail, { callstatus : this.calldetails.CallStatus  ,dateTime : call_time , conversationduration : this.calldetails.ConversationDuration ,callerNumber : this.calldetails.Number , isBlocked : this.calldetails.isBlocked})
									this.blocked_list.push(this.blocked_detail)
									// this.userUid = this.Udata.uid
									// console.log(typeof(this.detail))
									console.log('blocked details',this.blocked_list)
								}
							})
						})
						.catch((error) => {
							console.log("Error getting documents: ", error);
						})
						})
					.catch((error) => {blocked_data
						console.error(error);
					})
			}
    }
	}
	</script>