<template>
	<v-app >
		<div>
			<v-container  fluid>
				<v-layout align-center justify-center>
					<v-flex xs12 sm12 md10>
						<v-card class="mx-auto">
							<v-row no-gutters>
								<v-col cols="12">
									<div>
									</div>
									<div class="text-center" v-if="main">
										<div class="ml-4 mr-4">
											<h3 class="mt-6 ml-5 mb-5 text-center">Addons</h3>
										</div>
										<v-btn class="mr-4 white--text" @click='add_user()' color='light-blue darken-1'> Add User</v-btn>
										<v-btn class="mr-4 white--text" @click='add_virtual()' color='light-blue darken-1
										' > Add Virtual Number</v-btn>
									</div>	
									<div v-if='numberList'>
										<h4 class="mt-6 text-center">Choose your business number</h4>
										
										<p class="mt-6 text-center">Time Remaining : {{ Math.floor(timerCount/60) }} mins {{ timerCount%60 }} sec</p>
										<div class="ml-5 mr-5">
											<v-progress-linear  color="deep-orange" height="14" :value= 'value' striped ></v-progress-linear>
										</div>
										<v-btn-toggle v-model="toggle_none" >
											<div class="ml-3 mt-5 text-center">
												<v-btn v-for="item in V_numbers" :key="item" class="ml-1 mr-4 mb-5 red--text" outlined color='white' width="44%">{{item}}</v-btn>
											</div>
										</v-btn-toggle>
										<div class="text-center">							
											<v-btn class="mr-4 mb-6 white--text text-center" width="40%" @click.prevent='reserveNumber()' color='light-blue darken-1'> Next </v-btn>
										</div>
									</div>
								</v-col>
							</v-row>
						</v-card>
					</v-flex>
				</v-layout>
			</v-container>
		</div>
	</v-app>
</template>

<script src="https://checkout.razorpay.com/v1/checkout.js"></script>
<script>
import firebase from 'firebase'
import { db } from '@/main.js';
// import moment from 'moment'
  export default {
		created() {
			firebase.auth().onAuthStateChanged(user => {
				if (user) {
					console.log("logged user details",user)
					this.uid = user.uid
					this.phno = user.phoneNumber.slice(3)
					console.log("page call logs il user id", this.uid)
					console.log("page call logs il user number", this.phno)
					db.collection('users').where("uid" , "==" , this.uid).get().then((querySnapshot) => {
						querySnapshot.forEach((doc) => {
							console.log(doc.id, " => ", doc.data());
							let user_details = doc.data()
							this.virtual_number = user_details.virtualNumber
							this.name = user_details.FirstName
							this.state = user_details.State
							this.gst = user_details.Gstin
							this.email = user_details.Email
							console.log(user_details.role)
						})
					})
					.catch((error) => {
						console.log("Error getting documents: ", error);
					});
				}
			})
		},

		data: () => ({
			uid : '',
			phno : '',
      email: '',
      gstin: '',
      state: '',
      main : true,
      numberList: false,
			timerCount : 180,
			value : 100,
			V_numbers : [],
			listingId: '',
			toggle_none : null,
			overlay: true,
    }),
    methods :{

			add_user() {
				var token = localStorage.getItem('token');
				const user_details = {
					url: 'https://asia-south1-tringpartner-v2.cloudfunctions.net/tpv2/addon/payment',
					method: 'POST',
					headers: { 
						'token': token,
						'Content-Type': 'application/json'
					},
					data: {
						owner_uid: this.uid,
						type: 'USER',
					},
				}
				console.log(user_details)
				this.$axios(user_details)
					.then((response) => {
						console.log(response)
						console.log('order_id',response)
						console.log(response.data.order_id)
						this.order_id = response.data.order_id
				var options = {
					key: "rzp_test_ThdwdEPh3QCHbo",
					
					order_id: this.order_id,
					name: this.name,
					currency: "INR", // Optional. Same as the Order currency
					description: "Purchase Description",

					handler:  (response) =>{
						console.log(response);
					},
					prefill: {
            name: this.name,
            email: this.email,
            contact: this.phno
					},
					notes: {
            address: this.address
					},
					theme: {
            color: "#3399cc"
					}
				};
				console.log(options)
				var rzp1 = new Razorpay(options);
				rzp1.open();

					})

					.catch((error) => {user_details
						console.error(error);
					})
			},

			add_virtual() {
				// var token = localStorage.getItem('token');
				this.numberList = true
				this.main = false
				const details = {
						url: 'https://asia-south1-tringpartner-v2.cloudfunctions.net/tpv2/virtualNumber/list',
						method: 'POST',
						data: {
							uid: this.uid,
							phoneNumber: this.phno
						},
					}
					console.log(details)
					this.$axios(details)
						.then((response) => {
							this.overlay = false
							this.V_numbers = response.data.numbers
							this.timerCount = response.data.Seconds
							this.listingId = response.data.listingId
							this.value = 100 - (0.55*(180-this.timerCount))
							console.log(this.value)
							console.log(response)
							console.log(response.data.numbers)
							console.log(response.data.Seconds)
							this.progressbarTimer(this.value)
						})
						.catch((error) => {
							console.error(error);
						})

				},
				
			reserveNumber() {
				let virtualNumber = this.V_numbers[this.toggle_none]
				console.log(virtualNumber)
				const reserve = {
					url: 'https://asia-south1-tringpartner-v2.cloudfunctions.net/tpv2/virtualNumber/reserve',
					method: 'POST',

					data: {
						uid: this.uid,
						phoneNumber: this.phno,
						virtualNumber: virtualNumber,
						listingId: this.listingId
					},
				}
				console.log(reserve)
        this.$axios(reserve)
					.then((response) => {
						console.log(response)
				var token = localStorage.getItem('token');
				const user_details = {
					url: 'https://asia-south1-tringpartner-v2.cloudfunctions.net/tpv2/addon/payment',
					method: 'POST',
					headers: { 
						'token': token,
						'Content-Type': 'application/json'
					},
					data: {
						owner_uid: this.uid,
						type: 'USER',
					},
				}
				console.log(user_details)
				this.$axios(user_details)
					.then((response) => {
						console.log(response)
						console.log('order_id',response)
						console.log(response.data.order_id)
						this.order_id = response.data.order_id
				var options = {
					key: "rzp_test_ThdwdEPh3QCHbo",
					
					order_id: this.order_id,
					name: this.name,
					currency: "INR", // Optional. Same as the Order currency
					description: "Purchase Description",

					handler:  (response) =>{
						console.log(response);
					},
					prefill: {
            name: this.name,
            email: this.email,
            contact: this.phno
					},
					notes: {
            address: this.address
					},
					theme: {
            color: "#3399cc"
					}
				};
				console.log(options)
				var rzp1 = new Razorpay(options);
				rzp1.open();

					})

					.catch((error) => {user_details
						console.error(error);
					})

					})
					.catch((error) => {
						console.error(error);
					})

			},
			progressbarTimer(value) {
				// console.log(value)
				if (value > 0) {
					var myVar = setInterval(() => {
						if (this.value > 0) {
						console.log('before',this.value)
						this.value = this.value - 0.55
						// console.log('after',this.value)
						// this.value2 = this.value2 - 10
						this.timerCount--;
						// console.log('after',this.timerCount)
					} else {
					this.value = 0
					this.timerCount = 0
				}
					}, 1000);

				}
				else {
					clearInterval(myVar);
					this.value = 0
					this.timerCount = 0
				}
			}

    }
	}
	</script>